.content {
  display: flex;
  padding: 50px;
  background-color: rgb(226, 226, 226);
  max-width: 600px;
  margin: 0 auto;
  margin-bottom: 60px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}
.statistics {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(255, 255, 255);
  margin: 0 auto;
  border: 1px solid;
}
.title {
  padding: 15px 30px;
  margin-top: 0;
  margin-bottom: 0;
}
.stat_list {
  display: flex;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
}
.item {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-color: rgb(5, 199, 134);
  padding: 15px;
  border: 1px solid;
}
.label {
  display: block;
  max-width: 45px;
  width: 100%;
  color: white;
}
.percentage {
  color: white;
  font-size: larger;
  display: block;
  width: 40px;
}
